<script>
	import { Popup , Toast } from 'vant';
	import NXXToken from '../../../static/NXXToken.json';
	export default{
		components:{ Popup , Toast },
		data () {
			return {
				details: '',
				BreachGABA: 0,
				BreachNXX: 0,
			}
		},
		methods:{
			async init(){
				//
				var id = this.$route.query.id;
				this.details = await this.$contract.pledge.methods.LenderOrdersState(id).call();
				for (let index in this.details.expPledgeContracts) {
					var test = new this.$web3.eth.Contract(NXXToken.abi, this.details.expPledgeContracts[index]);
					var name = await test.methods.symbol().call()
					if(name == 'GABA'){
						this.BreachGABA = this.$fromWei(this.details.expAmounts[index])
					}else{
						this.BreachNXX = this.$fromWei(this.details.expAmounts[index])
					}
				}
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

