<template>
	<div class="van-tabbar-page box">
		<div class="lenderTop">
			<div>
				<div>
					<span></span>
					<div>{{totalAmounts}} USDT</div>
				</div>
				<div>
					<span></span>
					<div>{{delteAmounts}} USDT</div>
				</div>
			</div>
			<div @click="$router.push('./lenderJoin')">
				<img src='../../../assets/lenderBox.png' />
				<span></span>
				<icon name='arrow' ></icon>
			</div>
		</div>
		
		<div class="MyLenderContract">
			<div class="MyLenderContractTitle">
				<div></div>
				<span></span>
			</div>
			<div class="MyLenderContractListBox">
				<div class="MyLenderContractList" v-for="(item,index) in LenderList" :key='index' @click="$router.push({path:'./lenderDetails',query:{id:item.id}})">
					<div>
						<div>{{item.id}}</div>
						<div>{{item.time}}</div>
					</div>
					<div>
						<div><span></span><span>{{item.totalAmounts}} USDT</span></div>
						<div><span></span><span>{{item.delteAmounts}} USDT</span></div>
						<icon name='arrow' ></icon>
					</div>
					<div>
						<button class="bule" v-if='item.state == "1"' @click="LenderRollbackUSDT(item.id)"></button>
						<button class="gray" v-else-if='item.state == "3"'></button>
						<button class="gray" v-else></button>
					</div>
				</div>
				<!-- <div class="MyLenderContractList">
					<div>
						<div>20200316001</div>
						<div>03-05 17:50</div>
					</div>
					<div>
						<div><span></span><span>150 USDT</span></div>
						<div><span></span><span>0.00 USDT</span></div>
						<icon name='arrow' ></icon>
					</div>
					<div>
						<button class="gray"></button>
					</div>
				</div> -->
			</div>
		</div>
	</div>
</template>

<script>
	import { icon , Popup , Toast } from 'vant';
	export default{
		components:{ icon , Popup , Toast },
		data () {
			return {
				totalAmounts: 0,
				delteAmounts: 0,
				LenderList: [],
			}
		},
		methods:{
			async init(){
				this.LenderList = [];
				//
				var ids = await this.$contract.pledge.methods.LenderListTotalInfo(this.$address).call();
				ids.ids.forEach(async item=>{
					var list = await this.$contract.pledge.methods.LenderInfo(item).call();
					if(list.state == '1'){
						this.totalAmounts += this.$fromWei(list.totalAmount)
						this.delteAmounts += this.$fromWei(list.totalAmount) - this.$fromWei(list.currentPaymented)
					}
					this.LenderList.unshift({
						'totalAmounts': this.$fromWei(list.totalAmount),
						'delteAmounts': this.$fromWei(list.totalAmount) - this.$fromWei(list.currentPaymented),
						'id': item,
						'time': this.$toTime(list.createTime,'Y-M-D H:i'),
						'state': list.state
					})
				})
				console.log(this.LenderList)
				// console.log(this.$contract.pledge.methods.LenderOrdersState)
			},
			LenderRollbackUSDT(id){
				this.$contract.pledge.methods.LenderRollbackUSDT(id).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang='scss' scoped>
	.box{
		padding: 20px 26px 0;
		
		.lenderTop{
			width: 100%;
			height: 314px;
			background-color: rgba($color: #2A78C8, $alpha: 0.61);
			border-radius: 10px;
			display: flex;
			flex-direction: column;
			padding: 0 16px;
			
			&>div:nth-child(1){
				width: 100%;
				height: 206px;
				font-size: 40px;
				display: flex;
				padding: 24px 0;
				border-bottom: 1px dotted #020C1E;
				
				&>div{
					width: 50%;
					height: 100%;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					
					&>div{
						font-size: 30px;
					}
				}
				
				&>div:nth-child(1){
					border-right: 1px dotted #020C1E;
				}
			}
			
			&>div:nth-child(2){
				width: 100%;
				flex: 1;
				display: flex;
				justify-content: center;
				align-items: center;
				font-size: 40px;
				position: relative;
				
				&>img{					
					width: 114px;
					height: 96px;
					margin-top: 10px;
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
				}
				
				&>.van-icon{
					font-size: 25px;
					position: absolute;
					right: 0;
					top: 50%;
					transform: translateY(-50%);
				}
				
			}
		}
	}
	
	.MyLenderContract{
		margin-top: 20px;
		
		&>.MyLenderContractTitle{
			display: flex;
			align-items: center;
			font-size: 32px;
			margin-bottom: 14px;
			
			&>div{
				width: 6px;
				height: 36px;
				background-color: #3A9CFF;				
				margin-right: 12px;
			}
		}
		
		&>.MyLenderContractListBox{
			.MyLenderContractList{
				width: 100%;
				height: 246px;
				background-color: rgba($color: #1C528A, $alpha: 0.67);
				border-radius: 10px;
				margin-bottom: 16px;
				display: flex;
				flex-direction: column;
				
				&>div:nth-child(1){
					display: flex;
					justify-content: space-between;
					align-items: center;
					color: #8E99A4;
					font-size: 24px;
					padding: 9px  18px 9px 26px;
				}
				
				&>div:nth-child(2){
					width: 100%;
					height: 100px;
					font-size: 28px;
					background-color: rgba($color: #011740, $alpha: 0.59);
					display: flex;
					flex-direction: column;
					justify-content: center;
					padding: 0 18px 0 26px;
					position: relative;
					
					&>div{
						text-align: left;
						
						&>span:nth-child(1){
							margin-right: 26px;
						}
					}
					
					.van-icon{
						font-size: 25px;
						position: absolute;
						right: 10px;
						top: 50%;
						transform: translateY(-50%);
					}
				}
				
				&>div:nth-child(3){
					flex: 1;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					
					button{
						width: 140px;
						height: 48px;
						border-radius: 8px;
						font-size: 24px;
						border: none;
					}
					
					.bule{
						background-color: #3A9CFF;
					}
					
					.gray{
						background-color: #537C95;
					}
					
				}
			}
		}
	}
</style>
