<template>
	<div class="van-tabbar-page box">
		<div class="Detection">
			<p></p>
			<div>
				<span> {{usdt}} USDT</span>
				<span> {{authorizedAccess}} USDT</span>
			</div>
			<div>
				<input type="number" placeholder="" v-model="authorizedAmount" />
				<button @click="usdtAuthorization"></button>
			</div>
			<div>
				<img src="../../../assets/details.png" />
				
			</div>
		</div>
		<div class="lender">
			<p></p>
			<div>
				<span>1 USDT = 0.3 GABA</span>
				<span>1 USDT = 0.01 NXX</span>
			</div>
			<div>
				<input type="number" placeholder="" v-model="usdtAmount" />
				<div>
					<img />
					USDT
				</div>
			</div>
			<div>  0.00  USDT</div>
			<div class="footer">
			    <checkbox class="checkbox" v-model="checked">
			        {{$t('IHaveRead')}}<span style="color: #4D95FC;">{{$t('RulesEngagement')}}</span>{{$t('involved')}}
			    </checkbox>
			    <button :disabled="!checked" @click="lenderUSDT"></button>
			</div>
		</div>
	</div>
</template>

<script>
	import { Popup , Toast , Checkbox } from 'vant';
	// import NXXToken from '../../../static/NXXToken.json';
	
	export default{
		components:{ Popup , Toast , Checkbox },
		data () {
			return {
				checked: false,
				authorizedAmount: '',
				availableAuthorizedAccess: 0.00,
				authorizedAccess: 0.00,
				usdtAmount: '',
				usdt: 0,
			}
		},
		methods:{
			async init(){
				// this.$contract.pledge.methods.PledgeItemList().call((err,result)=>{
				// 	console.log(result)
				// })
				
				// var test = new this.$web3.eth.Contract(NXXToken.abi, '0x6F2974762aca367F4e519264CEf90ecC9181E6D3');
				// test.methods.totalSupply().call((err,result)=>{
				// 	console.log(result)
				// })
				// test.methods.symbol().call((err,result)=>{
				// 	console.log(result)
				// })
				// console.log(test)
				
				// USDT
				this.authorizedAccess = this.$fromWei(await this.$contract.usdtToken.methods.allowance(this.$address,this.$contract.pledge._address).call({from:this.$address}))
				
				//USDT
				this.usdt = this.$fromWei(await this.$contract.usdtToken.methods.balanceOf(this.$address).call())
				// console.log(this.$contract.usdtToken.methods)
				// this.authorizedAccess  = Number(authorizedAccess) ? false : true ;
				// this.$contract.usdtToken.methods.
			},
			usdtAuthorization(){
				this.$contract.usdtToken.methods.approve(this.$contract.pledge._address,this.$web3.utils.toWei(this.authorizedAmount.toString(),'ether')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			lenderUSDT(){
				this.$contract.pledge.methods.LendingUSDT(this.$web3.utils.toWei(this.usdtAmount.toString(),'ether')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		padding: 28px 26px 0;
		
		.Detection{
			width: 100%;
			height: 432px;
			background-color: rgba($color: #2A78C8, $alpha: 0.61);
			border-radius: 10px;
			padding: 46px 28px 0;
			display: flex;
			flex-direction: column;
			
			&>p{
				font-size: 44px;
			}
			
			&>div:nth-child(2){
				display: flex;
				flex-direction: column;
				font-size: 32px;
				color: #F5F5F5;
				text-align: left;
				margin: 40px 0 18px;
			}
			
			&>div:nth-child(3){
				width: 100%;
				display: flex;
				font-size: 36px;
				justify-content: space-between;
				
				&>input{
					width: 440px;
					height: 108px;
					border: #3A9CFF solid 2px;
					border-radius: 10px;
					background-color: transparent;
					margin-right: 12px;
					padding-left: 14px;
				}
				
				&>button{
					flex: 1;
					height: 100%;
					background-color: #3A9CFF;
					border: none;
					border-radius: 10px;
				}
			}
			
			&>div:nth-child(4){
				display: flex;
				align-items: center;
				justify-content: center;
				font-size: 26px;
				color: #8E99A4;
				margin-top: 24px;
				
				&>img{
					width: 36px;
					height: 36px;
					margin-right: 10px;
				}
			}
		}
		
		.lender{
			width: 100%;
			height: 616px;
			background-color: rgba($color: #2A78C8, $alpha: 0.61);
			border-radius: 10px;
			padding: 42px 28px 0;
			display: flex;
			flex-direction: column;
			margin-top: 32px;
			
			&>p{
				font-size: 44px;
			}
			
			&>div:nth-child(2){
				display: flex;
				flex-direction: column;
				font-size: 32px;
				color: #F5F5F5;
				text-align: left;
				margin: 34px 0 18px;
			}
			
			&>div:nth-child(3){
				width: 100%;
				display: flex;
				font-size: 36px;
				justify-content: space-between;
				border: #3A9CFF solid 2px;
				border-radius: 10px;
				
				&>input{
					width: 408px;
					height: 108px;
					border: none;
					background-color: transparent;
					padding-left: 14px;
				}
				
				&>div{
					flex: 1;
					height: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					border-left: #3A9CFF 2px solid;
				}
			}
			
			&>div:nth-child(4){
				font-size: 28px;
				color: #6E8AA5;
				text-align: left;
				margin: 20px 0 36px;
			}
			
			.footer {
				width: 100%;
			    display: flex;
			    flex-direction: column;
			    align-items: flex-start;
				font-size: 26px;
				
			    &>button {
					margin-top: 30px;
					width: 100%;
			        border: none;
			        outline: none;
			        background-color: #0083F1;
			        height: 80px;
			        font-size: 32px;
			        color: #fff;
			        border-radius: 8px;
			        margin-bottom: 40px;
			    }
				
				&>button:disabled{
					opacity: .5;
				}
			}
		}
	}
</style>
