			<div>{{this.endTime.substr(2,1)}}</div>
			<p>:</p>
			<div>{{this.endTime.substr(4,1)}}</div>
			<div>{{this.endTime.substr(5,1)}}</div>
			<p>:</p>
			<div>{{this.endTime.substr(7,1)}}</div>
			<div>{{this.endTime.substr(8,1)}}</div>
		</div>
		<div class="Crowdfunding">
			<p v-if='list.status == "1"'></p>
			<p v-else>{{$t('CountdownToCrowdfunding')}}</p>
			<div class="CrowdfundingBox">
