						</div>
					</div>
					<div>
						<span>{{ (totalAmount - currentAmount).toFixed(2) }}</span>
						<span>GABA</span>
					</div>
					<div v-if="list.status == '4'" class="gray">{{$t('over')}}</div>
					<div v-if="list.status == '2'" class="bule">{{$t('InCrowdfunding')}}</div>
					<div v-if="list.status == '1'">{{$t('WeReInRehearsal')}}</div>
				</div>
			</div>
			<div class="CrowdfundingButton">
				<button @click="$router.push({path:'/Join',query:{oid:list.oid}})">{{$t('Partake')}}</button>
				<button @click="$router.push('/record/joinRecord')">{{$t('Records')}}</button>
			</div>
		</div>
		<div class="Statistics">
			<p>{{$t('Statistics')}}</p>
			<div class="StatisticsBox">
				<div>
					<p>{{this.$fromWei(CapitalPool,2)}}</p>
