					<div @click="Jump('https://www.token.im/?from=singlemessage')">
						<img src="../../assets/guide01.png"/>
						<span>imToken</span>
					</div>
					<div @click="Jump('https://www.huobiwallet.com/?from=singlemessage')">
						<img src="../../assets/guide02.png"/>
						<span>Huobi</span>
					</div>
					<div @click="Jump('https://trustwallet.com/?from=singlemessage')">
						<img src="../../assets/guide03.png"/>
						<span>Trust</span>
					</div>
					<div @click="Jump('http://mathwallet.net/cn/?from=singlemessage')">
