</template>

<script>
import { Popup , Toast } from 'vant';
import { guide  } from '../../static/initData'
import axios from 'axios'

export default {
  name: "home",
  components: { Popup , Toast },
  data() {
    return {
		authorizedAccess : false,
		grandPrize: false,
		invite: false,
		guide: false,
		bindCodeStatus: false,
		bindCode: '',
		generateCode: '',
		list: [],
		endTime: '000:00:00',
		CapitalPool: '0',
		DestructionPool: '0',
		luckAssetsPool: '0',
		totalAmount: '0',
		currentAmount: '0',
		rewardTotal: '0',
		LatestCrowdfundingList: [],
		luckyAmout: '0',
		fromBlock: 0,
	}
  },
  methods: {
	async init(){
		this.guide = guide
		this.CapitalPool = await this.$contract.gabaToken.methods.balanceOf(this.$contract.mainContract._address).call()
		this.DestructionPool = await this.$contract.gabaToken.methods.balanceOf('0x000000000000000000000000000000000000dead').call()
		this.luckAssetsPool = (await this.$contract.mainContract.methods.BalanceOfLuckAssetPoolAtRID(65535).call()).ridTotal
		this.rewardTotal = (await this.$contract.mainContract.methods.BalanceOfLuckAssetPoolAtRID(65535).call()).rewardTotal
		var authorizedAccess = await this.$contract.mainContract.methods.CurrentAllowance().call({from:this.$address})
		this.authorizedAccess  = Number(authorizedAccess) ? false : true ;
		if(!this.authorizedAccess){
			var invite = await this.$contract.recommend.methods.AddressToShortCode(this.$address).call();
			this.invite = parseInt(invite,16) ? false : true ;
			function GetQueryString(name) {
			    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
			    var r = window.location.search.substr(1).match(reg);
			    if (r != null) return unescape(r[2]); return null;
			}
			if(GetQueryString('code')){
				this.bindCode = GetQueryString('code')
				this.bindCodeStatus = true;
			}
		}
		
		// var id  = (await this.$contract.mainContract.methods.RoundStatusAt().call()).oid
		this.$contract.mainContract.methods.RoundStatusAt(65535).call((err,result)=>{
			this.list = result;
			this.totalAmount = this.$fromWei(result.totalAmount,2)
			this.currentAmount = this.$fromWei(result.currentAmount,2)
			
			if(this.list.status == '1'){
				var endTime = setInterval(()=>{
					if(new Date().getTime()/1000 > this.list.startTime){
						window.location.reload()
						return clearInterval(endTime) 
					}
					this.endTime = this.timeDown(this.list.startTime)
				},1000)
			}else{
				if(new Date().getTime()/1000 > this.list.endTime){
					this.endTime = '000:00:00'
					return clearInterval(endTime) 
				}
				var endTime = setInterval(()=>{
					if(new Date().getTime()/1000 > this.list.endTime){
						this.endTime = '000:00:00'
						return clearInterval(endTime) 
					}
					this.endTime = this.timeDown(this.list.endTime)
				},1000)
			}
		})
		
	},
	async bind(){
		var bindCode = this.$web3.utils.stringToHex(this.bindCode);
		var generateCode = this.$web3.utils.stringToHex(this.generateCode);
		
		if(this.bindCode.length<6){
			return Toast.fail(this.$t('ren'))
		}else if(this.generateCode.length<6){
			return Toast.fail(this.$t('ma'))
		}
		
		var toAddress = await this.$contract.recommend.methods.ShortCodeToAddress(bindCode).call();
		var thisAddress = await this.$contract.recommend.methods.ShortCodeToAddress(generateCode).call();
		
		if( !parseInt(toAddress,16) ){
			return Toast.fail(this.$t('WrongInviter'))
		}else if(parseInt(thisAddress,16)){
			return Toast.fail(this.$t('CodeBeen'))
		}
		// console.log(toAddress,generateCode)
		this.$contract.recommend.methods.BindRelationEx(toAddress,generateCode).send({
			from : this.$address
		}).on('transactionHash' , txHash =>{
			console.log(txHash)
		}).on('error' , err=>{
			console.log(err)
		}).on('receipt' , result=>{
			this.init()
		})
		
	},
	authorizedAccessFun(){
		// console.log(this.$contract.mainContractAddress)
		this.$contract.gabaToken.methods.approve(this.$contract.mainContractAddress,this.$web3.utils.toWei('100000000','ether')).send({
			from : this.$address
		}).on('transactionHash' , txHash =>{
			console.log(txHash)
		}).on('error' , err=>{
			console.log(err)
		}).on('receipt' , result=>{
			this.init()
		})
	},
	timeDown(endDateStr){
		function add(m){return m<10?'0'+m:m }
		function add1(m){return m >= 100 ? m : m >= 10 ? '0'+m : '00'+m}
		//
		var endDate = endDateStr*1000;
		//
		var nowDate = new Date();
		//
		var totalSeconds = parseInt((endDate - nowDate) / 1000)
		//
		var day=parseInt(totalSeconds/3600/24);
		//
		var modulo = totalSeconds % (60 * 60 * 24);
			
		//
		var hours = Math.floor((modulo / (60 * 60)));
		modulo = modulo % (60 * 60);
		//
		var minutes = Math.floor(modulo / 60);
		//
		var seconds = modulo % 60;
		return add1(hours+(day*24)) + ":" + add(minutes) + ":" + add(seconds);
	},
	Jump(href){
		window.location.href = href
	},
	LatestParticipation(fromBlock = this.fromBlock,toBlock = 'latest'){
		axios.get('https://api-cn.etherscan.com/api',{
			params: {
				"module":'logs',
				"action":'getLogs',
				"fromBlock":fromBlock,
				"toBlock":toBlock,
				"address":this.$contract.mainContract._address,
				"topic0":'0xa281b5b87d919fea384481d7cc84b9464f629dc7a4da2ba24c43fcaa5ce3aa6b',
				"topic3": this.$web3.eth.abi.encodeParameter('uint8','1'),
				"apikey":'APJQ2KZQX7GQMD9FTUK986WSBRVEB7TUYU',
			}
		}).then( result => {
			result.data.result.forEach( item => {
				var data = this.$web3.eth.abi.decodeParameters(['uint256','int256'],item.data);
				var box = {
					time: data[0],
					amout: data[1],
					address: this.$web3.eth.abi.decodeParameters(['address'],item.topics[1])[0],
					oid: this.$web3.eth.abi.decodeParameters(['uint256'],item.topics[2])[0],
					type: this.$web3.eth.abi.decodeParameters(['uint8'],item.topics[3])[0],
				};
				this.LatestCrowdfundingList.push(box)
				this.LatestCrowdfundingList.sort(function(a,b){
					return b.time - a.time
				})
			})
			// console.log(this.LatestCrowdfundingList)
			if(result.data.result.length > 0){
				var ToBlock = this.fromBlock;
				this.fromBlock -= 10800;
				this.LatestParticipation(this.fromBlock,ToBlock)
			}
		}).catch( err => {
			console.log(err)
		});
	}
  },
  async mounted() {
		this.init()
		this.LatestCrowdfundingList = [];
		
		this.fromBlock = await this.$web3.eth.getBlockNumber().then();
		this.fromBlock -= 3600;
		
		this.LatestParticipation()
		
		var address = this.$address;
		axios.get('https://api-cn.etherscan.com/api',{
			params: {
				"module":'logs',
				"action":'getLogs',
				"fromBlock":9514338,
				"toBlock":'latest',
				"address":this.$contract.mainContract._address,
				"topic0":'0xbd60c87b8021edfe53d2767927920d66fc70d7b6cee571e84ec3a2cd9a80303e',
				"topic1": address,
				"topic3": this.$web3.eth.abi.encodeParameter('uint16','100'),
				"apikey":'APJQ2KZQX7GQMD9FTUK986WSBRVEB7TUYU',
			}
		}).then( result => {
			result.data.result.forEach( item => {
				var data = this.$web3.eth.abi.decodeParameters(['uint256','int256'],item.data);
				var time = data[0];
				var amout = data[1];
				var StartDay = new Date(new Date().toLocaleDateString()).getTime()/1000,
					EndDay = new Date(new Date().toLocaleDateString()).getTime()/1000 + (24*60*60-1);
					
				if(time < EndDay && time > StartDay){
					this.grandPrize = true;
					this.luckyAmout = amout;
				}
			})
		}).catch( err => {
			console.log(err)
		});
  }
};
</script>

