		height: 528px;
		// background-color: #3F8DE0;
		// border-radius: 20px;
		font-size: 32px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		
		&>.inviteBoxTop{
			width: 100%;
			height: 400px;
			background-color: #3F8DE0;
			border-radius: 20px;
			font-size: 40px;
			padding: 0 16px;
			
			div{
				text-align: left;
				padding: 34px 0 20px;
				border-bottom: 2px solid #004075;
			}
			
			input{
				width: 100%;
				height: 90px;
				border-radius: 10px;
				background-color: #004075;
				border: none;
				padding:0 20px;
				color: #3F8DE0;
			}
			
			input:nth-child(2){
				margin: 35px 0;
			}
		}
		
		button{
			width: 260px;
			height: 74px;
			background-color: red;
			border: none;
			background-color: #3F8DE0;
			border-radius: 40px;
		}
	}
	
	.guideBox{
		width: 730px;
		height: 354px;
		background-color: rgba(12, 84, 164 , .8);
		border-radius: 10px;
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding-top: 20px;
		
		&>img{
			position: absolute;
			width: 39px;
			top: 20px;
			right: 38px;
		}
		
		&>span{
			font-size: 40px;
		}
		
		&>.guideText{
			font-size: 32px;
			width: 100%;
			text-align: left;
			padding-left: 46px;
			position: relative;
			margin-top: 26px;
		}
		
		&>.guideText:after{
			content: '';
			position: absolute;
			width: 10px;
			height: 36px;
			background-color: #1296DB;
			left: 24px;
			top: 50%;
			transform: translateY(-50%);
		}
		
		&>.guideImg{
			width: 100%;
			display: flex;
			// align-items: center;
			justify-content: space-between;
			padding: 0 42px;
			margin-top: 24px;
			font-size: 28px;
			
			&>div{
				flex: 1;
				display: flex;
				flex-direction: column;
				align-items: center;
			}
			
			&>div:nth-child(1){
				padding-top: 10px;
				
				&>img{
					width: 100px;
					height: 100px;
					margin-bottom: 3px;
				}
			}
			
			&>div:nth-child(2){
				padding-top: 25px;
				
				&>img{
					width: 49px;
					height: 75px;
					margin-bottom: 14px;
				}
			}
			
			&>div:nth-child(3){
				padding-top: 17px;
				
				&>img{
					width: 82px;
					height: 82px;
					margin-bottom: 14px;
				}
			}
			
			&>div:nth-child(4){
				&>img{
					width: 114px;
					height: 114px;
				}
			}
		}
	}
</style>