<template>
	<div class="box">
		<div class="exchangeFrom">
			<div class="Description">{{$t('ProtocolExchange')}}&nbsp;1 GABA = {{ONEF_Prop}} ONEF</div>
			<div class="Description">{{$t('RemainingQuota')}}&nbsp;{{ isChangeable ? RemainingONEF : 0 }} GABA</div>
			
			<div class="exchangeInputBox">
				<input type='number' :placeholder="$t('ExchangeQuota')" v-model="exchangeNumber" @input="exchangeInput" />
				<div>
					<img src="../../assets/exchangeGABA.png" />
					<span>GABA</span>
				</div>
			</div>
			
			<div class="exchangeBalance">
				<div>					
					<span>{{$t('Available')}}</span>
					<span>{{gaba}}</span>
					<span>GABA</span>
				</div>
			</div>
			
			<div class="exchangeInputBox">
				<input type='number' :placeholder="$t('ReceivingQuota')" disabled="disabled" v-model="exchangeONEF" />
				<div>
					<img src="../../assets/exchangeONEF.png" />
					<span>ONEF</span>
				</div>
			</div>
			
			<div class="exchangeBalance">
				<div>
					<span>{{$t('Available')}}</span>
					<span>{{onef}}</span>
					<span>ONEF</span>
				</div>
				<div @click="changeDetails=true">
					<img src="../../assets/details.png"/>
					<span>{{$t('GettoKnowDetails')}}</span>
				</div>
			</div>
			
			<button @click="exchange">{{$t('exchange')}}</button>
		</div>
		
		<div class="Statistics">
			<div class="StatisticsBox">
				<div>
					<p>{{exchangePool}}</p>
					<span>{{$t('ExchangePool')}}/ONEF</span>
				</div>
				<div @click="WithdrawLockedGaba">
					<p>{{lockupPool}}</p>
					<span>{{$t('LockPool')}}/GABA</span>
				</div>
			</div>
		</div>
		
		<div class="exchangeRecord">
			<p>{{$t('ExchangeRecord')}}</p>
			<div class="exchangRecordContent">
				<div class="exchangRecordList">
					<div><span class="bule">{{$t('Transacting')}}</span><span>+150 ONEF</span></div>
					<div><span>03.05 17:50</span><span>-5 GABA</span></div>
				</div>
				<div class="exchangRecordList">
					<div><span class="green">{{$t('SuccessfulExchange')}}</span><span>+150 ONEF</span></div>
					<div><span>03.05 17:50</span><span>-5 GABA</span></div>
				</div>
				<div class="exchangRecordList">
					<div><span class="red">{{$t('FailedExchange')}}</span><span>+150 ONEF</span></div>
					<div><span>03.05 17:50</span><span>-5 GABA</span></div>
				</div>
				<div class="noRecord" v-if="Record">
					<img src="../../assets/noRecord.png"/>
					<span>{{$t('NoTransaction')}}</span>
				</div>
			</div>
		</div>
		
		<popup v-model="authorizedAccess" >
			<div class="authorizedAccessBox">
				<div class="authorizedAccessTop">
					<div><img src="../../assets/yellowRing.png"/></div>
					<p>{{$t('Participation1')}}</p>
				</div>
				<div class="authorizedAccessButton" @click="onefAuthorization">{{$t('Authorizations')}}</div>
			</div>
		</popup>
		
		<popup v-model="changeDetails" >
			<div class="changeDetails">
				<p>{{$t('AboutExchange')}}ONEF</p>
				<p>{{$t('ExchangeDetailsText1')}}</p>
				<div><span>1</span><span>{{$t('ExchangeDetailsText2')}}</span></div>
				<div><span>2</span><span>{{$t('ExchangeDetailsText3')}}</span></div>
				<div><span>3</span><span>{{$t('ExchangeDetailsText4')}}</span></div>
				<img @click="changeDetails=false" src="../../assets/out.png" />
			</div>
		</popup>
	</div>
</template>

<script>
	import { Popup , Toast } from 'vant';
	
	export default {
		components: { Popup , Toast },
		data(){
			return{
				authorizedAccess: false,
				Record: false,
				changeDetails: false,
				invite: false,
				isChangeable: true,
				ONEF_Prop: 0,
				exchangeNumber: '',
				exchangeONEF: '',
				RemainingONEF: 0,
				gaba: 0,
				onef: 0,
				exchangePool: 0,
				lockupPool: 0,
			}
		},
		methods:{
			async init(){
				this.ONEF_Prop = Number(await this.$contract.financialPlan.methods.GetGABAToONEFChangeRate().call())
				this.gaba = this.$fromWei(await this.$contract.gabaToken.methods.balanceOf(this.$address).call())
				this.onef = this.$fromWei(await this.$contract.onefToken.methods.balanceOf(this.$address).call())
				
				// ONEF
				this.$contract.financialPlan.methods.ChangeStateInfo().call((err,result)=>{
					var currentLockedAmount = this.$fromWei(result.currentLockedAmount);
					var changeQuotaMaxLimit = this.$fromWei(result.changeQuotaMaxLimit);
					
					//
					this.lockupPool = Math.floor(currentLockedAmount)
					
					//
					this.RemainingONEF = changeQuotaMaxLimit - currentLockedAmount;
					
					// true =    false = 
					this.isChangeable = result.isChangeable
					// console.log(result)
				})
				
				// 
				this.exchangePool = this.$fromWei(await this.$contract.onefToken.methods.balanceOf(this.$contract.financialPlan._address).call())-this.$fromWei(await this.$contract.financialPlan.methods.CurrentDepositedONEFTotalAmount().call());
				
				// ONEF
				var authorizedAccess = await this.$contract.gabaToken.methods.allowance(this.$address,this.$contract.financialPlan._address).call({from:this.$address})
				this.authorizedAccess  = Number(authorizedAccess) ? false : true ;
				
				//
				var invite = await this.$contract.recommend.methods.AddressToShortCode(this.$address).call();
				this.invite = parseInt(invite,16) ? false : true ;
				
			},
			exchangeInput(){
				var value = Number(this.exchangeNumber);
				if(value > this.gaba){
					value = this.gaba
					this.exchangeNumber = this.gaba
				}else if(value > this.RemainingONEF){
					value = this.RemainingONEF
					this.exchangeNumber = this.RemainingONEF
				}
				this.exchangeONEF = value * this.ONEF_Prop
			},
			exchange(){
				if(!this.isChangeable){
					return Toast.fail(this.$t('pauseExchange'))
				}else if(this.exchangeNumber == 0 || this.exchangeNumber == '0' || this.exchangeNumber == ''){
					return Toast.fail(this.$t('tips4'))
				}else if(Number(this.exchangeNumber) % 1 != 0){
					return Toast.fail(this.$t('tips5'))
				}else if(this.invite){
					return Toast.fail(this.$t('tips3'))
				}
				this.$contract.financialPlan.methods.DoChange(this.$toWei(this.exchangeNumber)).send({
					from: this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , console.error)
				.on('receipt' , result=>{
					console.log(result)
					this.init()
					this.exchangeNumber = ''
					this.exchangeONEF = ''
				})
			},
			onefAuthorization(){
				this.$contract.gabaToken.methods.approve(this.$contract.financialPlan._address,this.$web3.utils.toWei('100000000','ether')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			WithdrawLockedGaba(){
				this.$contract.financialPlan.methods.WithdrawLockedGaba().send({
					from: this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , console.error)
				.on('receipt' , result=>{
					this.init()
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.exchangeFrom{
		width: 100%;
		height: 593px;
		padding: 38px 36px;
		background-color: rgba($color: #2A78C8, $alpha: 0.61);
		font-size: 28px;
		border-radius: 10px;
		
		&>.Description{
			width: 100%;
			text-align: left;
			margin-bottom: 10px;
		}
		
		&>.exchangeInputBox{
			width: 100%;
			height: 100px;
			border-radius: 20px;
			border: 2px solid #3A9CFF;
			display: flex;
			margin-bottom: 14px;
			
			&>input{
				width: 420px;
				height: 100%;
				font-size: 32px;
				padding-left: 22px;
				background-color: transparent;
				border: none;
			}
			
			&>input::placeholder{
				color: #A6A6A6;
			}
			
			&>div{
				border-left: 2px solid #3A9CFF;
				flex: 1;
				display: flex;
				align-items: center;
				justify-content: center;
				
				&>img{
					width: 48px;
					height: 44px;
					margin-right: 26px;
				}
			}
		}
		
		&>.exchangeBalance{
			display: flex;
			justify-content: space-between;	
			align-items: center;
			margin-bottom: 26px;
			
			&>div:nth-child(1){
				&>span:nth-child(2){
					margin: 0 5px;
				}
			}
			
			&>div:nth-child(2){
				color: #8E99A4;
				display: flex;
				align-items: center;
				line-height: 1.26;
				
				&>img{
					width: 36px;
					margin-right: 8px;
				}
			}
		}
		
		&>button{
			width: 340px;
			height: 72px;
			border: none;
			background-color: #3F8DE0;
			border-radius: 5px;
		}
	}
	
	.Statistics{
		margin: 10px 0;
		// margin-bottom: 45px;
		
		&>.StatisticsBox{
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			
			&>div{
				width: 50%;
				height: 240px;
				
				background-size: 100%;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				font-size: 28px;
				
				&>p{
					font-size: 40px;
				}
			}
			
			&>div:nth-child(1){
				background-image: url(../../assets/StatisticsBoxTopLeft.png);
			}
			
			&>div:nth-child(2){
				background-image: url(../../assets/StatisticsBoxTopRight.png);
			}
		}
	}
	
	.exchangeRecord{
		width: 100%;
		height: 543px;
		background-color: rgba($color: #4A7EB5, $alpha: 0.39);
		border-radius: 10px;
		font-size: 28px;
		padding: 0 35px;
		display: flex;
		flex-direction: column;
		
		
		&>p{
			padding: 10px 0 8px;
		}
		
		&>.exchangRecordContent{
			flex: 1;
			
			&>.exchangRecordList{
				font-size: 24px;
				margin-bottom: 10px;
				
				&>div{
					width: 100%;
					display: flex;
					justify-content: space-between;
				}
			}
			
			&>.noRecord{
				font-size: 24px;
				color: #758D9C;
				display: flex;
				flex-direction: column;
				align-items: center;
				
				&>img{
					width: 67px;
					height: 85px;
				}
			}
		}
		
		.bule{
			color: #12B3FE;
		}
		
		.green{
			color: #19C580;
		}
		
		.red{
			color: #C56363;
		}
	}
	
	
	.van-popup--center{
		background: transparent;
	}
	
	.authorizedAccessBox{
		width: 430px;
		height: calc(330px + 54px + 74px);
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		
		&>div{
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
		
		.authorizedAccessTop{
			width: 100%;
			height: 330px;
			background: #3F8DE0;
			border-radius:20px;
			font-size: 32px;
			padding: 0 40px;
			
			&>div{
				width: 106px;
				height: 106px;
				border-radius: 50%;
				// border: 5px solid #FFCE00;
				margin-bottom: 30px;
				
				img{
					width: 106px;
					height: 106px;
				}
			}
		}
		
		.authorizedAccessButton{
			width: 260px;
			height: 74px;
			background: #3F8DE0;
			border-radius:40px;
			font-size: 32px;
		}
	}
	
	.changeDetails{
		width: 708px;
		height: 712px;
		background-color: rgba(16,79,153,.89);
		border-radius: 10px;
		font-size: 28px;
		padding: 42px 36px;
		text-align: left;
		position: relative;
		
		&>p:nth-child(1){
			margin-bottom: 20px;
		}
		
		&>div{
			display: flex;
			// line-height: 1;
			
			&>span:nth-child(1){
				display: inline-block;
				width: 30px;
				height: 30px;
				border-radius: 50%;
				border: 1px solid #fff;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-right: 15px;
				margin-top: 6px;
				padding-top: 1px;
			}
			
			&>span:nth-child(2){
				flex: 1;
			}
		}
		
		&>img{
			position: absolute;
			width: 40px;
			height: 40px;
			top: 42px;
			right: 42px;
		}
	}
</style>
